* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.container-stars {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.container-price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 48px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  box-shadow: 0 2px 4px #0000000d;
}

.header_logo {
  height: 40px;
}

.nav__list {
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.product {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
  display: flex;
}

.product:hover {
  scale: 1.2;
}

.product-photo {
  align-self: center;
  width: 160px;
  height: 134px;
}

.product-title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
}

.product:hover .product__title {
  color: #34568b;
}

.product-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product-reviews {
  font-size: 10px;
  line-height: 14px;
}

.product-price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-price-number {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  flex-shrink: 0;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.product-button:hover {
  cursor: pointer;
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  gap: 4px;
  padding: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(0), .stars__star:first-child, .stars__star:nth-child(2), .stars__star:nth-child(3), .stars__star:nth-child(4) {
  background-image: url("star-active.edda4c43.svg");
}

